<template>
  <div class="profilepage">
    <Headerbar></Headerbar>
    <!-- <div class="bgp"></div> -->
    <div class="userpic">
      <div class="pic" :style="{backgroundImage:'url('+userdata.pictureUrl+')'}"></div>
    </div>
    <div class="switchtab">
      <router-link
        :to="{
                name:'alreadysignup',
                params:{hostid:this.hostid}
            }"
      >
        <div ref="myact" class="myact" @click="switchtab('myact')">
          <i ref="myacti" class="ri-coupon-2-fill" @click="switchtab('myact')"></i>
          <span @click="switchtab('myact')">我的活動</span>
        </div>
      </router-link>
      <router-link
        :to="{
                name:'Userinfo',
                params:{hostid:this.hostid}
            }"
      >
        <div ref="profile" class="profile" @click="switchtab('profile')">
          <i ref="profilei" class="ri-user-fill" @click="switchtab('profile')"></i>
          <span @click="switchtab('profile')">個人資料</span>
        </div>
      </router-link>
    </div>
    <router-view></router-view>
    <Sidemenu v-show="showsidemenu"></Sidemenu>
  </div>
</template>

<script>
import Headerbar from "../components/Header";
// import Profilecontent from '../components/Profilecontent'
import Sidemenu from "../components/Sidemenu";
export default {
  data() {
    return {
      currentstatus: "profile"
    };
  },
  components: {
    Headerbar,
    Sidemenu
  },
  created() {},
  mounted() {
    let profile = this.$refs.profile;
    let profilei = this.$refs.profilei;
    let myact = this.$refs.myact;
    let myacti = this.$refs.myacti;
    console.log(this.$route.name);
    if (this.$route.name == "Userinfo" || this.$route.name == "profile") {
      profile.style.color = this.maincolor;
      profilei.style.color = this.maincolor;
      myact.style.color = "#cdcdcd";
      myacti.style.color = "#cdcdcd";
    } else if (
      this.$route.name == "alreadysignup" ||
      this.$route.name == "myact"
    ) {
      profile.style.color = "#cdcdcd";
      profilei.style.color = "#cdcdcd";
      myact.style.color = this.maincolor;
      myacti.style.color = this.maincolor;
    }
    this.$store.commit("sethostid", this.$route.params.hostid);
  },
  methods: {
    switchtab(val) {
      let profile = this.$refs.profile;
      let profilei = this.$refs.profilei;
      let myact = this.$refs.myact;
      let myacti = this.$refs.myacti;
      if (val == "profile") {
        profile.style.color = this.maincolor;
        profilei.style.color = this.maincolor;
        myact.style.color = "#cdcdcd";
        myacti.style.color = "#cdcdcd";

        this.$store.commit("setprofilecurrentpage", "profile");
      } else {
        this.$store.commit("setprofilecurrentpage", "myact");

        profile.style.color = "#cdcdcd";
        profilei.style.color = "#cdcdcd";
        myact.style.color = this.maincolor;
        myacti.style.color = this.maincolor;
      }
    }
  },
  computed: {
    maincolor() {
      return this.$store.state.maincolor;
    },
    profilecurrentpage() {
      return this.$store.state.profilecurrentpage;
    },
    userdata() {
      return this.$store.state.userprofiledata;
    },
    showsidemenu() {
      return this.$store.state.showsidemenu;
    },
    hostid() {
      return this.$store.state.hostid;
    }
  }
};
</script>

