<template>
  <div class="signupeventlist">
    <div class="title" v-show="signupevent.length==0">
      <div class="loader">
        <span>資料讀取中</span>
        <!-- <div class="wrap">
          <div ref="circle" class="circle"></div>
          <div class="circle"></div>
          <div class="circle"></div>
        </div> -->
      </div>
    </div>
    <div class="eventblockwrap">
      <eventblock
        :eventname="item.content[0]"
        :date="convertdate(item.content[2])"
        :time="item.content[3]"
        v-for="item of signupevent"
        :key="item.event"
      ></eventblock>
    </div>
  </div>
</template>

<script>
import eventblock from "../components/Profilepage-eventblock";
import axios from "axios";
import anime from "animejs/lib/anime.es.js";
export default {
  data() {
    return {
      menuopen: false,
      currenteventstatus: "signup",
      alleventlist: [],
      filtersignuplist: []
    };
  },
  created() {
    axios({
      method: "get",
      url:
        "https://script.google.com/macros/s/AKfycbzW4IbkVHdr_DCY3U65uBx4zV9cCmSBeU-FT1QsK_UbloDhniYPT-TGXQi5tky9M5UR/exec",
      params: {
        mode: "hostid",
        id: this.$route.params.hostid
      }
    }).then(response => {
      // console.log('有抓到', response.data)
      this.alleventlist = response.data;
      this.$store.dispatch("getUserEventinfo");
      // this.eventlist = response.data;
    });
  },
  mounted() {
    let circle = this.$refs.circle;
    anime({
      targets: circle,
      opcity: [1, 0],
      easing: "easeInOutQuad",
      loop: true,
      duration: 450
    });
  },
  methods: {
    opendropdown() {
      if (this.menuopen == false) {
        this.menuopen = true;
      } else {
        this.menuopen = false;
      }
    },
    clicked(val) {
      if (val == "history") {
        this.currenteventstatus = "history";
      } else {
        this.currenteventstatus = "signup";
      }
      this.menuopen = false;
    },
    convertdate(date) {
      let str = date;
      // console.log(str)
      let strlist = str.split("-");
      let result = (strlist[0] + "/" + strlist[1] + "/" + strlist[2]).substring(
        0,
        10
      );

      return result;
    },
    multiFilter(array, filters) {
      const filterKeys = Object.keys(filters);
      // filters all elements passing the criteria
      return array.filter(item => {
        // dynamically validate all filter criteria
        // console.log(item)
        return filterKeys.every(key => {
          //ignore when the filter is empty Anne
          if (!filters[key].length) return true;
          return !!~filters[key].indexOf(item[key]);
        });
      });
    }
  },
  computed: {
    hostid() {
      return this.$store.state.hostid;
    },
    usereventinfo() {
      const oldl = this.$store.state.usereventinfo.length;
      let newusereventinfo = Array(oldl)
        .fill()
        .map(() => Array());
      for (let i = 0; i < this.$store.state.usereventinfo.length; i++) {
        for (let j = 0; j < this.$store.state.usereventinfo[i].length; j++) {
          if (this.$store.state.usereventinfo[i][j] != "") {
            newusereventinfo[i].push(this.$store.state.usereventinfo[i][j]);
          } else {
            console.log("");
          }
        }
      }
      return newusereventinfo;
    },
    signupevent() {
      if (this.usereventinfo.length != 0) {
        //處理filter
        let eventfilters = {
          eventid: []
        };

        //把原本的eventinfo結構改成新的物件結構
        let newsignupevent = [];

        for (let i = 0; i < this.usereventinfo.length; i++) {
          let eventidindex = this.usereventinfo[i].length - 2;
          eventfilters.eventid.push(this.usereventinfo[i][eventidindex]);
        }
        console.log("filtereventlist", eventfilters);

        for (let i = 0; i < this.alleventlist.length; i++) {
          let obj = {
            eventid: "",
            content: []
          };
          for (let j = 0; j < this.alleventlist[i].length; j++) {
            //將除了id以外的內容塞到content當中
            if (j == this.alleventlist[i].length - 1) {
              obj.eventid = this.alleventlist[i][j];
            } else {
              obj.content.push(this.alleventlist[i][j]);
            }
          }
          newsignupevent.push(obj);
        }

        console.log("新的報名資料格式", newsignupevent);
        // eventfilters.eventid = new Set(eventfilters.eventid)
        // console.log('新的報名資料', newsignupevent)
        let filtersignuplist = this.multiFilter(newsignupevent, eventfilters);
        var today = new Date();
        let nowdays = new Date(
          today.getFullYear() +
            "/" +
            (today.getMonth() + 1) +
            "/" +
            today.getDate()
        );

        return filtersignuplist.filter(item => {
          let eventdate = new Date(this.convertdate(item.content[2]));
          if (eventdate > nowdays) {
            return item;
          }
        });
      } else {
        return [];
      }
    },
    userdata() {
      return this.$store.state.userprofiledata;
    }
  },
  components: {
    eventblock
  }
};
</script>

