<template>
  <div class="eventlistpage" ref="eventlistpage">
    <Headerbar></Headerbar>
    <br />
    <div class="searchbarwrap">
      <SearchbarRound @roundemitkeyword="catchkeyword"></SearchbarRound>
    </div>
    <div class="searchfilterwrap">
      <Searchfilter @emittypefilter="typefilter" @emittimefilter="timefilter"></Searchfilter>
    </div>

    <div class="result">
      <div v-if="keyword==''">關鍵字：無關鍵字</div>
      <div v-else>關鍵字：『{{keyword}}』</div>
      <br />
      <div v-if="currenttype==''">分類：全部分類</div>
      <div v-else>
        分類：
        <span class="currenttype" :style="{backgroundColor:maincolor}">{{currenttype}}</span>
      </div>
      <div v-if="eventtime==''">時間：全部時間</div>
      <div v-else>
        時間：
        <span>{{eventtime}}</span>
      </div>
    </div>
    <!-- <div class="catawrap">
        <catatag :class="{tagclick:currenttagindex==index}" @typehandler="selecttype" v-for="(item,index) of taglist" :key="index" :tagname="item" :tagindex=index></catatag>
    </div>-->

    <div class="eventlist">
      <h4 v-show="filtereventlist.length==0">目前沒有這個分類的活動呦！</h4>
      <Actblock
        v-show="item[5]=='否'||item[5]==''"
        v-for="item in filtereventlist"
        :key="item[18]"
        :name="item[0]"
        :fee="item[13]"
        :loc="item[4]"
        :date="convertdate(item[2])"
        :time="item[3]"
        :number="item[7]"
        :deadlinedate="convertdate(item[8])"
        :type="item[6]"
        :eventid="item[18]"
        :pic="item[1]"
      ></Actblock>
      <!-- <Actblockeventlist v-for="item of filtereventlist" :key="item[17]" :name="item[0]" :loc="item[4]" :date="convertdate(item[2])" :time="item[3]" :actin="item[7]" :waitingnum="item[8]" :type="item[6]" :id="item[17]" :pic="item[1]"></Actblockeventlist> -->
    </div>
    <Footer></Footer>
    <Sidemenu v-show="showsidemenu"></Sidemenu>
    <div class="loadingmask" v-show="loadingcircle">
      <div class="loader" :style="{borderTop:'8px solid'+maincolor}"></div>
    </div>
  </div>
</template>

<script>
import Headerbar from "../components/Header";
import Actblock from "../components/Actblock";
import Searchfilter from "../components/Searchfilter";
// import Searchbar from '../components/Searchbar'
import Footer from "../components/footer";
import SearchbarRound from "../components/searchbar-round";
// import catatag from '../components/catatag'
// import Actblockeventlist from '../components/Actblock-eventlist'
import axios from "axios";
import Sidemenu from "../components/Sidemenu";

export default {
  data() {
    return {
      taglist: ["親子", "手做", "戶外", "食物", "3C"],
      eventlist: [],
      keyword: this.$store.state.searchkeyword,
      currenttype: "",
      eventtime: "",
      currenttagindex: "-1",
      loadingcircle: true,
      watchlist: []
    };
  },
  components: {
    Headerbar,
    // Searchbar,
    // catatag,
    // Actblockeventlist,
    Sidemenu,
    SearchbarRound,
    Actblock,
    Searchfilter,
    Footer
  },
  created() {
    axios({
      method: "get",
      url:
        "https://script.google.com/macros/s/AKfycbzW4IbkVHdr_DCY3U65uBx4zV9cCmSBeU-FT1QsK_UbloDhniYPT-TGXQi5tky9M5UR/exec",
      params: {
        mode: "hostid",
        id: this.$route.params.hostid
      }
    }).then(response => {
      this.loadingcircle = false;
      this.eventlist = response.data;
    });
    if (window.sessionStorage.getItem("hostinfo")) {
      var hostinfo = window.sessionStorage.getItem("hostinfo");
      this.$store.commit("sethostinfo", JSON.parse(hostinfo));
    }
  },
  mounted() {
    this.$store.dispatch("gethostinfo");
    window.sessionStorage.setItem("hostinfo", JSON.stringify(this.hostinfo));
  },
  methods: {
    typefilter(val) {
      this.currenttype = val;
    },
    timefilter(val) {
      // this.eventtime = val;
      //今天的時間
      var day2 = new Date();
      day2.setTime(day2.getTime());
      var s2 =
        day2.getFullYear() + "-" + (day2.getMonth() + 1) + "-" + day2.getDate();
      //明天的時間
      var day3 = new Date();
      day3.setTime(day3.getTime() + 24 * 60 * 60 * 1000);
      var s3 =
        day3.getFullYear() + "-" + (day3.getMonth() + 1) + "-" + day3.getDate();

      var day4 = new Date();
      day4.setTime(day4.getTime() + 24 * 60 * 60 * 1000 * 2);
      var s4 =
        day4.getFullYear() + "-" + (day4.getMonth() + 1) + "-" + day4.getDate();
      var day = day2.getDay();
      var minusday = 7 - day;

      var weekend = new Date();
      weekend.setTime(weekend.getTime() + 24 * 60 * 60 * 1000 * minusday);
      var sweekend =
        weekend.getFullYear() +
        "-" +
        (weekend.getMonth() + 1) +
        "-" +
        weekend.getDate();

      var nexweek = new Date();
      nexweek.setTime(nexweek.getTime() + 24 * 60 * 60 * 1000 * (minusday + 7));
      var snexweek =
        nexweek.getFullYear() +
        "-" +
        (nexweek.getMonth() + 1) +
        "-" +
        nexweek.getDate();

      switch (val) {
        case "今天":
          this.eventtime = new Date(s2);

          break;
        case "明天":
          this.eventtime = new Date(s3);

          break;
        case "後天":
          this.eventtime = new Date(s4);

          break;
        case "本週":
          this.eventtime = new Date(sweekend);

          break;
        case "下週":
          this.eventtime = new Date(snexweek);

          break;
      }
      // return this.filtereventlist
    },
    searchkeyword(val) {
      this.keyword = val;
    },
    catchkeyword(val) {
      this.keyword = val;
    },
    selecttype(val, index) {
      if (this.currenttagindex == index) {
        this.currenttagindex = -1;
        this.currenttype = "";
      } else {
        this.currenttagindex = index;
        this.currenttype = val;
      }
    },
    convertdate(date) {
      let str = date;

      let strlist = str.split("-");
      let result = (strlist[0] + "/" + strlist[1] + "/" + strlist[2]).substring(
        0,
        10
      );

      return result;
    },
    filterkeyword(alleventlist) {
      return alleventlist.filter(item => {
        console.log(item[0].includes(this.keyword));
        if (item[0].includes(this.keyword) == true) {
          return item;
        }
      });
    },
    filtertime(alleventlist) {
      return alleventlist.filter(item => {
        return new Date(item[2]) <= this.eventtime;
      });
    },
    filtertype(alleventlist) {
      return alleventlist.filter(item => {
        return item[6] == this.currenttype;
      });
    }
  },
  computed: {
    hostinfo() {
      return this.$store.state.hostinfo;
    },
    maincolor() {
      return this.$store.state.maincolor;
    },
    filtereventlist() {
      const { keyword, currenttype, eventtime } = this;
      // const returnarr = [];
      if (keyword != "" && currenttype != "" && eventtime != "") {
        return this.eventlist
          .filter(item => item[0].includes(keyword) == true)
          .filter(item => item[6] == currenttype)
          .filter(item => new Date(item[2]) <= eventtime);
      }
      if (keyword != "" && currenttype != "") {
        console.log("4");
        return this.filterkeyword(this.filtertype(this.eventlist));
      }
      if (keyword != "" && currenttype != "" && eventtime != "") {
        console.log("4");
        return this.filterkeyword(
          this.filtertype(this.filtertime(this.eventlist))
        );
      }
      if (currenttype != "" && eventtime != "") {
        return this.filtertype(this.filtertime(this.eventlist));
      }
      if (eventtime != "" && keyword != "") {
        return this.filterkeyword(this.filtertime(this.eventlist));
      }
      if (keyword != "") {
        console.log("1");
        return this.filterkeyword(this.eventlist);
      }
      if (currenttype != "") {
        console.log("2");
        return this.filtertype(this.eventlist);
      }
      if (eventtime != "") {
        console.log("3");
        return this.filtertime(this.eventlist);
      } else {
        return this.eventlist;
      }

      // console.log("5");
    },
    showsidemenu() {
      return this.$store.state.showsidemenu;
    }
  }
};
</script>

